import { String } from "../../Model/String";
import { Injectable } from "@angular/core";
import { ServicesProvider } from "../services/services";
import { FileExportHKTableListResponse } from "../../Model/Response/FileExportHKTableListResponse";
import { FileExportHKTableResponse } from "../../Model/Response/FileExportHKTableResponse";
import { FileExportHKTableViewModel } from '../../Model/ViewModel/FileExportHKTableViewModel';
import { StringResponse } from "../../Model/Response/StringResponse";
import { NumberResponse } from "../../Model/Response/NumberResponse";

/*
  Generated class for the FileExportHKTableServiceProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/

@Injectable()
export class FileExportHkTableServicesProvider {
  private FileExportHKTables: FileExportHKTableListResponse;
  private FileExportHKTable: FileExportHKTableResponse;
   ctl: string;

  constructor(
  public Services: ServicesProvider
  ) {
    this.ctl = "FileExportHKTable";
    console.log("Hello FileExportHKTableServiceProvider Provider");
  }

  public GetListsByAsync(PageSize: number, PageNumber: number, keyword: string,exec_file_seq:number,order_type:boolean) {
    var sub_url: string;
     sub_url = "FileExportHKTable?PageSize={1}&PageNumber={2}&keyword={3}&exec_file_seq={4}&order_type={5}";
    sub_url = String.Format(sub_url, this.ctl, PageSize, PageNumber, keyword,exec_file_seq,order_type);
    return this.Services.GetAsync(sub_url,this.ctl,true).map(item => {
      this.FileExportHKTables = new FileExportHKTableListResponse({
        Message: item.Message,
        DidError: item.DidError,
        ErrorMessage: item.ErrorMessage,
        PageSize: item.PageSize,
        PageNumber: item.PageNumber,
        TotalRows: item.TotalRows,
        PageRows: item.PageRows,
        TotalPages: item.TotalPages,
        Model: item.Model
      });
      return this.FileExportHKTables;
    });
  }



  public PostPutAsync(FileExportHKTableViewModel:FileExportHKTableViewModel,mode:string) {
    var sub_url: string;

    if(mode==="POST")
      sub_url = "FileExportHKTable";
    else
    sub_url = "FileExportHKTable?exec_file_seq={1}&schemaname={2}&tablename={3}&db_conn_id={4}&parameter_column_group={5}&src_export_type_key={6}";
    sub_url = String.Format(sub_url, this.ctl,FileExportHKTableViewModel.exec_file_seq,FileExportHKTableViewModel.schemaname, FileExportHKTableViewModel.tablename,FileExportHKTableViewModel.db_conn_id,FileExportHKTableViewModel.parameter_column_group,FileExportHKTableViewModel.src_export_type_key);
    return this.Services.PostPutAsync(sub_url, this.ctl, true,FileExportHKTableViewModel,mode).map(
      item => {
        this.FileExportHKTable = new FileExportHKTableResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });
        return this.FileExportHKTable;
      }
    );
  }

  public DeleteAsync(exec_file_seq:number,schemaname:string, tablename:string,db_conn_id:string,parameter_column_group:string,src_export_type_key:string) {
    var sub_url: string;
    sub_url = "FileExportHKTable?exec_file_seq={1}&schemaname={2}&tablename={3}&db_conn_id={4}&parameter_column_group={5}&src_export_type_key={6}";
    sub_url = String.Format(sub_url, this.ctl,exec_file_seq,schemaname, tablename,db_conn_id,parameter_column_group,src_export_type_key);

    return this.Services.DeleteAsync(sub_url, this.ctl, true).map(
      item => {
        return  new StringResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: ""
        });
      }
    );
  }

  public GetMaxExecSeqAsync(exec_file_seq:number) {
    var sub_url: string;
    sub_url = "FileExportHKTable/get_max_exec_seq?exec_file_seq={1}";
    sub_url = String.Format(sub_url, this.ctl,exec_file_seq);

    return this.Services.GetAsync(sub_url, this.ctl, true).map(
      item => {
        return  new NumberResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });
      }
    );
  }


  public GetSqlAsync(exec_file_seq:number,schemaname:string, tablename:string,db_conn_id:string,parameter_column_group:string,src_export_type_key:string,method:string) {
    var sub_url: string;
    sub_url = "FileExportTable/get_{7}_sql?exec_file_seq={1}&schemaname={2}&tablename={3}&db_conn_id={4}&parameter_column_group={5}&src_export_type_key={6}";
    sub_url = String.Format(sub_url, this.ctl,exec_file_seq,schemaname, tablename,db_conn_id,parameter_column_group,src_export_type_key,method);

    return this.Services.GetAsync(sub_url, this.ctl, true).map(
      item => {
        return  new StringResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });
      }
    );
  }
}



